@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
*{margin:0;padding:0;outline:0;box-sizing:border-box}html,body,#root{height:100%;background-color:#333}html .app,body .app,#root .app{font-family:Roboto, sans-serif}html .app .gallery,body .app .gallery,#root .app .gallery{display:flex;position:fixed;top:0;right:0;left:0;height:300px;z-index:2;overflow-x:scroll;opacity:0.65;padding:0 5px}html .app .gallery img,body .app .gallery img,#root .app .gallery img{width:200px;margin:0 2px}html .app .modalPreview,body .app .modalPreview,#root .app .modalPreview{display:flex;justify-content:center;align-items:center;position:fixed;top:0;right:0;bottom:0;left:0;width:100%;z-index:3;background-color:rgba(0,0,0,0.4)}html .app .modalPreview .previewContainer,body .app .modalPreview .previewContainer,#root .app .modalPreview .previewContainer{position:relative}html .app .modalPreview .previewContainer img,body .app .modalPreview .previewContainer img,#root .app .modalPreview .previewContainer img{width:600px;height:900px}html .app .modalPreview .previewContainer .closeButton,body .app .modalPreview .previewContainer .closeButton,#root .app .modalPreview .previewContainer .closeButton{display:flex;flex-direction:column;justify-content:center;align-items:center;width:60px;height:60px;position:absolute;top:0;right:0;background-color:rgba(0,0,0,0.75)}html .app .modalPreview .previewContainer .closeButton span,body .app .modalPreview .previewContainer .closeButton span,#root .app .modalPreview .previewContainer .closeButton span{display:block;background-color:#fff;width:30px;height:2px}html .app .modalPreview .previewContainer .closeButton span:nth-child(1),body .app .modalPreview .previewContainer .closeButton span:nth-child(1),#root .app .modalPreview .previewContainer .closeButton span:nth-child(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);position:absolute}html .app .modalPreview .previewContainer .closeButton span:nth-child(2),body .app .modalPreview .previewContainer .closeButton span:nth-child(2),#root .app .modalPreview .previewContainer .closeButton span:nth-child(2){-webkit-transform:rotate(-45deg);transform:rotate(-45deg);position:absolute}html .app .errorScreen,body .app .errorScreen,#root .app .errorScreen{display:flex;justify-content:center;align-items:center;position:fixed;top:0;right:0;bottom:0;left:0;background-color:#212121;z-index:2;display:none}html .app .errorScreen span,body .app .errorScreen span,#root .app .errorScreen span{color:#fff;font-size:20px;line-height:30px;max-width:80%;text-align:center}

